}
.el-table .el-table__cell.is-center {
  text-align: center;
}

.el-table .el-table__cell.is-right {
  text-align: right;
}

.el-table .el-table__cell.gutter {
  width: 15px;
  border-right-width: 0;
  border-bottom-width: 0;
