}
.el-table::before, .el-table--group::after, .el-table--border::after {
  content: "";
  position: absolute;
  background-color: #EBEEF5;
  z-index: 1;
}

.el-table--group::after, .el-table--border::after {
  top: 0;
  right: 0;
  width: 1px;
  height: 100%;
}

.el-table::before {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
}
.el-table--border {
  border-right: none;
  border-bottom: none;
}
.el-table--border.el-loading-parent--relative {
  border-color: transparent;
}
.el-table--border .el-table__cell {
  border-right: 1px solid #EBEEF5;
}
.el-table--border .el-table__cell:first-child .cell {
  padding-left: 10px;
}
.el-table--border th.el-table__cell.gutter:last-of-type {
  border-bottom: 1px solid #EBEEF5;
  border-bottom-width: 1px;
}
.el-table--border th.el-table__cell {
  border-bottom: 1px solid #EBEEF5;
}

.el-table--hidden {
  visibility: hidden;
}

.el-table__fixed, .el-table__fixed-right {
  position: absolute;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.12);
}
.el-table__fixed::before, .el-table__fixed-right::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #EBEEF5;
  z-index: 4;
}

.el-table__fixed-right-patch {
  position: absolute;
  top: -1px;
  right: 0;
  background-color: #FFFFFF;
  border-bottom: 1px solid #EBEEF5;
}

.el-table__fixed-right {
  top: 0;
  left: auto;
  right: 0;
}
.el-table__fixed-right .el-table__fixed-header-wrapper,
.el-table__fixed-right .el-table__fixed-body-wrapper,
.el-table__fixed-right .el-table__fixed-footer-wrapper {
  left: auto;
  right: 0;
}

.el-table__fixed-header-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}

.el-table__fixed-footer-wrapper {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3;
}
.el-table__fixed-footer-wrapper tbody td.el-table__cell {
  border-top: 1px solid #EBEEF5;
  background-color: #F5F7FA;
  color: #606266;
}

.el-table__fixed-body-wrapper {
  position: absolute;
  left: 0;
  top: 37px;
  overflow: hidden;
  z-index: 3;
}

.el-table__header-wrapper, .el-table__body-wrapper, .el-table__footer-wrapper {
  width: 100%;
}

.el-table__footer-wrapper {
  margin-top: -1px;
}
.el-table__footer-wrapper td.el-table__cell {
  border-top: 1px solid #EBEEF5;
}

.el-table__header, .el-table__body, .el-table__footer {
  table-layout: fixed;
  border-collapse: separate;
}

.el-table__header-wrapper, .el-table__footer-wrapper {
  overflow: hidden;
}
.el-table__header-wrapper tbody td.el-table__cell, .el-table__footer-wrapper tbody td.el-table__cell {
  background-color: #F5F7FA;
  color: #606266;
}

.el-table__body-wrapper {
  overflow: hidden;
  position: relative;
}
.el-table__body-wrapper.is-scrolling-none ~ .el-table__fixed,
.el-table__body-wrapper.is-scrolling-none ~ .el-table__fixed-right {
  box-shadow: none;
}

.el-table__body-wrapper.is-scrolling-left ~ .el-table__fixed {
  box-shadow: none;
}

.el-table__body-wrapper.is-scrolling-right ~ .el-table__fixed-right {
  box-shadow: none;
}

.el-table__body-wrapper .el-table--border.is-scrolling-right ~ .el-table__fixed-right {
  border-left: 1px solid #EBEEF5;
}

.el-table__body-wrapper .el-table--border.is-scrolling-left ~ .el-table__fixed {
  border-right: 1px solid #EBEEF5;
}

.el-table .caret-wrapper {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  height: 34px;
  width: 24px;
  vertical-align: middle;
  cursor: pointer;
  overflow: initial;
  position: relative;
}
.el-table .sort-caret {
  width: 0;
  height: 0;
  border: solid 5px transparent;
  position: absolute;
  left: 7px;
}
.el-table .sort-caret.ascending {
  border-bottom-color: #C0C4CC;
  top: 5px;
}
.el-table .sort-caret.descending {
  border-top-color: #C0C4CC;
  bottom: 7px;
}
.el-table .ascending .sort-caret.ascending {
  border-bottom-color: #409EFF;
}
.el-table .descending .sort-caret.descending {
  border-top-color: #409EFF;
}
.el-table .hidden-columns {
  visibility: hidden;
  position: absolute;
  z-index: -1;
}
.el-table--striped .el-table__body tr.el-table__row--striped td.el-table__cell {
  background: #FAFAFA;
}
.el-table--striped .el-table__body tr.el-table__row--striped.current-row td.el-table__cell {
  background-color: #ecf5ff;
}

.el-table__body tr.hover-row > td.el-table__cell, .el-table__body tr.hover-row.current-row > td.el-table__cell, .el-table__body tr.hover-row.el-table__row--striped > td.el-table__cell, .el-table__body tr.hover-row.el-table__row--striped.current-row > td.el-table__cell {
  background-color: #F5F7FA;
}
.el-table__body tr.current-row > td.el-table__cell {
  background-color: #ecf5ff;
}

.el-table__column-resize-proxy {
  position: absolute;
  left: 200px;
  top: 0;
  bottom: 0;
  width: 0;
  border-left: 1px solid #EBEEF5;
  z-index: 10;
}

.el-table__column-filter-trigger {
  display: inline-block;
  line-height: 34px;
  cursor: pointer;
}
.el-table__column-filter-trigger i {
  color: #909399;
  font-size: 12px;
  transform: scale(0.75);
}

.el-table--enable-row-transition .el-table__body td.el-table__cell {
  transition: background-color 0.25s ease;
}

.el-table--enable-row-hover .el-table__body tr:hover > td.el-table__cell {
  background-color: #F5F7FA;
}

.el-table--fluid-height .el-table__fixed,
.el-table--fluid-height .el-table__fixed-right {
  bottom: 0;
  overflow: hidden;
}

.el-table [class*=el-table__row--level] .el-table__expand-icon {
  display: inline-block;
  width: 20px;
  line-height: 20px;
  height: 20px;
  text-align: center;
  margin-right: 3px;
}

