  padding-left: 14px;
  padding-right: 14px;
}

.el-table-filter {
  border: solid 1px #EBEEF5;
  border-radius: 2px;
  background-color: #FFFFFF;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin: 2px 0;
