.el-textarea__inner:hover {
  border-color: #C0C4CC;
}
.el-textarea__inner:focus {
  outline: none;
  border-color: #409EFF;
}

.el-textarea .el-input__count {
  color: #909399;
  background: #FFFFFF;
  position: absolute;
  font-size: 12px;
  bottom: 5px;
  right: 10px;
}
