  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 4px;
  opacity: 0;
  transition: opacity 120ms ease-out;
}
.el-scrollbar__bar.is-vertical {
  width: 6px;
  top: 2px;
}
